.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  position: absolute;
  width: 100%;
  left: 0;
  right: 0;
  z-index: 2;
}
@media only screen and (max-width: 769px) {
  .header__wrap-menu {
    position: fixed;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.9);
    right: 0;
    transition: all 1s;
    z-index: -1;
    top: -9999px;
  }
  .header__wrap-menu_active {
    z-index: 1;
    top: 0;
    transition: all 0.5s;
  }
  .header .menu {
    flex-direction: column;
    align-items: flex-end;
    display: flex;
    padding-top: 80px;
    margin: 0 auto;
    max-width: 510px;
  }
}
@media only screen and (max-width: 769px) and (max-width: 579px) {
  .header .menu {
    max-width: 290px;
  }
}
@media only screen and (max-width: 769px) {
  .header .menu__link {
    margin-right: 0;
  }
  .header .menu__link:hover {
    border: none;
  }
}

.burger {
  display: none;
}
@media only screen and (max-width: 769px) {
  .burger {
    display: block;
    position: relative;
    width: 30px;
    height: 3px;
    border-radius: 2px;
    border: 0;
    background: #0f133c;
    padding: 0;
  }
  .burger:before, .burger:after {
    content: "";
    display: block;
    width: 30px;
    height: 3px;
    background: #0f133c;
    position: absolute;
  }
  .burger:after {
    bottom: 6px;
  }
  .burger:before {
    top: 6px;
  }
  .burger_active {
    background: transparent;
    z-index: 10;
  }
  .burger_active::before {
    transform: rotate(45deg);
    top: auto;
    transition: all 1s;
    background: #ffffff;
  }
  .burger_active::after {
    transform: rotate(-45deg);
    bottom: auto;
    transition: all 1s;
    background: #ffffff;
  }
}

