}
.form__button-clear {
  background: url("./public/images/excursion/close.png") no-repeat left top;
  width: 12.5px;
  height: 12.5px;
  outline: none;
  border: none;
  cursor: pointer;
  position: absolute;
  right: 0;
  top: 7px;
}
.form__button {
