.map {
  margin-top: 80px;
  position: relative;

  @include media(1169px) {
    margin-top: 50px;
  }

  @include media(769px) {
    margin-top: 30px;
  }

  &__map-wrapper {
    margin-top: 30px;
    height: 700px;
    position: relative;
  }

  &__billet {
    height: 260px;
    background-color: #ffffff;
    position: absolute;
    width: 100%;
    z-index: 1;
    top: calc(50% - 160px);

    @include media(959px) {
      display: none;
    }
  }
}

.address {
  z-index: 2;
  position: absolute;
  top: calc(50% - 160px);
  left: calc((100% - 1100px) / 2);
  right: auto;
  height: 260px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 60px 0 ;
  box-sizing: border-box;

  @include media(1169px) {
    left: calc((100% - 930px) / 2);
  }

  @include media(959px) {
    display: none;
  }

  &__title {
    color: #0F133C;
    font-weight: 700;
    font-size: 20px;
    line-height: 23.48px;
  }

  &__desc {
    color: #8185A8;
    line-height: 21.6px;
    font-size: 16px;
    font-weight: 200;
    font-family: $inter;
  }

  &__city-address {
    background: url("./public/images/map/location.png") no-repeat left top;
    padding-left: 28px;
    color: #0F133C;
    font-size: 16px;
    font-family: $inter;
    font-weight: 200;
    line-height: 21.6px;
    margin-top: 20px;
  }

  &__phone {
    width: fit-content;
    background: url("./public/images/map/phone.png") no-repeat left top;
    padding-left: 28px;
    color: #0F133C;
    font-size: 16px;
    font-family: $inter;
    font-weight: 200;
    line-height: 21.6px;
    text-decoration: none;
    transition: color 0.5s ease-out;
    margin-top: 10px;
    &:hover {
      color: #FF7018;
    }
  }
}

.billet-mobile {
  display: none;
  padding: 25px 0;
  position: absolute;
  width: 100%;
  height: 58px;
  bottom: 0;
  background-color: #ffffff;
  @include media(959px) {
    display: block;
  }
  @include media(579px) {
    padding: 15px 0;
    height: 114px;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include media(579px) {
      flex-direction: column;
    }
  }

  &__city-address {
    background: url("./public/images/map/location.png") no-repeat left top;
    padding-left: 28px;
    color: #0F133C;
    font-size: 16px;
    font-family: $inter;
    font-weight: 200;
    line-height: 16.2px;
    padding-bottom: 7px;
    @include media(579px) {
      font-size: 12px;
      br {
        display: none;
      }
    }
  }

  &__phone {
      width: fit-content;
      background: url("./public/images/map/phone.png") no-repeat left top;
      color: #0F133C;
      font-size: 12px;
      font-family: $inter;
      font-weight: normal;
      line-height: 16.2px;
      text-decoration: none;
      transition: color 0.5s ease-out;
      padding: 5px 28px;
      &:hover {
        color: #FF7018;
      }
  }

  &__contacts {
    @include media(579px) {
      text-align: center;
      margin-bottom: 15px;
    }
  }
  &__title {
    color: #0F133C;
    font-size: 16px;
    line-height: 19px;
    font-weight: bold;
    margin-bottom: 4px;
  }
  &__desc {
    color: #8185A8;
    font-weight: 300;
    font-size: 12px;
    line-height: 16.2px;
  }
}

.map-wrap {
  height: 700px;
  margin-top: 30px;

  @include media(1169px) {
    height: 560px;
    margin-top: 20px;
  }

  @include media(959px) {
    height: 430px;
  }
}
